.dashboard_services h3 {
    position: relative;
    margin-bottom: 2em !important;
    padding-bottom: 5px;
}

.dashboard_services h3:after{position: absolute;
    width: 100px;
    content: "";
    left: 0;
    bottom: -5px;
    right: 0;
    margin: auto;
    border-bottom: 2px solid #97d3e0;
}

.dashboard_services button#save_button {
    border-radius: 28px !important;
    background: none;
    color: #0c78ce;
    font-size: 14px !important;
    transition: 0.3s;
    padding: 10px 35px !important;
}

.dashboard_services button#save_button:hover { background-color: #1d62dd; color: #fff; }
.dashboard_services .__react_component_tooltip.type-dark {
    color: #fff;
    background-color: #fbb145;
}

.dashboard_services .__react_component_tooltip.type-dark.place-right:after {
    border-right-color: #fbb145;}